import React, { useState } from 'react'
import MangaForm from './components/mangaform'
import ChapterView from './components/chapterview'
import Help from './components/help'
import mangadexAPI from './services/mgapi'
import { Container, Row, Col } from 'react-bootstrap'

const App = () => {
    const [ manga, setManga ] = useState({})
    const [ chapter, setChapter] = useState({})
    const [ mangaID, setMangaID ] = useState('7139')
    const [ chapterID, setChapterID ] = useState('67377')

    const handleMangaIDChange = event => {
        setMangaID(event.target.value)
    }

    const handleChapterIDChange = event => {
        setChapterID(event.target.value)
    }

    const displayChapter = event => {
        event.preventDefault()
        console.log('SUBMITTED!')

        mangadexAPI
            .getManga(mangaID)
            .then(data => {
                setManga(data)
            })

        mangadexAPI
            .getChapter(chapterID)
            .then(data => {
                setChapter(data)
            })

        setMangaID('')
        setChapterID('')
    }

    return (
        <Container className='container'>
            <Row style={{backgroundColor: '#191970'}}>
                <Col className="form" sm={12} md={5} lg={4}>
                    <h1><span style={{color: '#00FA9A'}}>Mangadex</span>terity</h1>
                    <MangaForm
                        displayChapter={displayChapter}
                        newMangaID={mangaID}
                        newChapterID={chapterID}
                        handleMangaIDChange={handleMangaIDChange}
                        handleChapterIDChange={handleChapterIDChange}
                    />
                </Col>
                <Col className="help" sm={12} md={7} lg={8}>
                    <Help />
                </Col>
            </Row>
            <Row style={{padding: '40px'}}>
                <ChapterView
                    manga={manga}
                    chapter={chapter}
                />
            </Row>
        </Container>
    )
}

export default App