import React from 'react'
import { Image, Container, Row, Col } from 'react-bootstrap'

const ChapterView = ({ manga, chapter }) => {
    const pages = chapter.page_array
    const mangaInfo = manga.Manga
    if (mangaInfo && pages) {
        return (
            <Container>
                <Row>
                    <h2>{mangaInfo.title} by {mangaInfo.author}</h2>
                </Row>
                <Row>
                    <h3>Chapter {chapter.chapter}: {chapter.title}</h3>
                </Row>
                <Row>
                    {pages.map(page => 
                        <Col xs={12}>
                            <Image className="page" src={page} fluid />
                        </Col>
                    )}
                </Row>
            </Container>
        )
    } else {
        return (
            <div></div>
        )
    }
}

export default ChapterView